@keyframes menu-sticky {
  0% {
    opacity: 0;
    transform: translateY(100%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

.menu-sticky {
  min-height: 50px; }
  .menu-sticky ul {
    display: grid;
    grid-gap: 1px;
    grid-template-columns: repeat(4, 1fr);
    padding: 0;
    margin: 0; }
  .menu-sticky li {
    display: inline-block;
    padding: 0;
    margin: 0;
    background: #F4F4F4; }
  .menu-sticky__sticky {
    background: #E8E8E8;
    z-index: 100;
    animation-duration: 0.3s;
    animation-timing-function: ease-out; }
  .menu-sticky__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #F4F4F4;
    padding: 10px;
    text-align: center;
    text-decoration: none;
    color: #000;
    font-size: 10px;
    font-weight: 600; }
    .menu-sticky__item__name, .menu-sticky__item__icon {
      display: block; }
    .menu-sticky__item__icon {
      display: flex;
      align-items: center;
      justify-content: center;
      color: #312E76;
      font-size: 2em;
      line-height: 0; }
    .menu-sticky__item:focus, .menu-sticky__item:active, .menu-sticky__item.is-active {
      color: #fff;
      background: #312E76; }
      .menu-sticky__item:focus .menu-sticky__item__icon, .menu-sticky__item:active .menu-sticky__item__icon, .menu-sticky__item.is-active .menu-sticky__item__icon {
        color: currentColor; }
  .menu-sticky.menu-sticky--active .menu-sticky__sticky {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    animation-name: menu-sticky; }
  @supports (padding-bottom: env(safe-area-inset-bottom)) {
    .menu-sticky li {
      padding-bottom: env(safe-area-inset-bottom); } }
