@keyframes menu-app-item {
  0% {
    opacity: 0;
    transform: translateX(-5px); }
  50% {
    opacity: 1;
    transform: translateX(5px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

.menu-app {
  color: #000;
  background: #fff; }
  .menu-app ul,
  .menu-app li {
    padding: 0;
    margin: 0; }
  .menu-app ul {
    display: block; }
  .menu-app li {
    display: none;
    border-bottom: 1px solid #F6F6F6;
    animation: menu-app-item 0.3s ease-out; }
    .menu-app li.is-visible {
      display: block; }
  .menu-app__head, .menu-app__item {
    font-size: 14px;
    line-height: 30px;
    padding: 10px 15px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .menu-app__head {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-weight: 600;
    border-bottom: 1px solid #F6F6F6;
    cursor: pointer; }
  .menu-app__head-icon {
    flex: 0 0 1em;
    width: 1em;
    height: 1em;
    margin-right: 5px;
    position: relative; }
  .menu-app__item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    text-decoration: none;
    color: #202020;
    font-weight: 500; }
    .menu-app__item__icon, .menu-app__item__image {
      flex: 0 0 30px;
      position: relative; }
    .menu-app__item__icon {
      font-size: 20px;
      margin-left: 10px; }
    .menu-app__item__image {
      font-size: 0;
      line-height: 0;
      text-align: center;
      margin-right: 10px; }
      .menu-app__item__image img {
        max-height: 30px; }
    .menu-app__item__title {
      flex: 1 1 auto; }
    .menu-app__item:active, .menu-app__item.is-active {
      background: #F6F6F6; }
