.grid-catalog {
  display: grid;
  align-items: stretch;
  grid-template-columns: repeat(2, 1fr);
  background: #FCFCFC;
  border-radius: 15px;
  overflow: hidden; }
  .grid-catalog__item {
    display: block;
    color: #000;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
    padding: 15px;
    text-decoration: none; }
    .grid-catalog__item[href] {
      color: #312E76; }
    .grid-catalog__item:nth-child(4n + 1), .grid-catalog__item:nth-child(4n + 4) {
      background: #F6F6F6; }
    .grid-catalog__item__icon, .grid-catalog__item__img {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      margin-bottom: 10px;
      height: 50px; }
    .grid-catalog__item__icon {
      font-size: 50px; }
    .grid-catalog__item__img {
      font-size: 0;
      line-height: 0; }
      .grid-catalog__item__img img {
        max-height: 50px; }
