@keyframes header-nav {
  0% {
    opacity: 0;
    transform: translateX(-100%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes header-search {
  0% {
    opacity: 0;
    transform: translateY(10px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes header-overlay {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.header {
  position: relative;
  z-index: 100;
  min-height: 70px; }
  .header__search, .header__menu, .header__overlay {
    animation-duration: 0.3s;
    animation-timing-function: ease-out; }
  .header__sticky {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start; }
  .header__top {
    flex: 0 0 70px;
    height: 70px;
    background: #F4F4F4;
    padding: 15px 0;
    position: relative;
    z-index: 100; }
  .header__top-inner {
    display: flex;
    align-items: center;
    justify-content: center; }
  .header__overlay, .header__search, .header__menu {
    display: none; }
  .header__overlay {
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    animation-name: header-overlay; }
  .header__search {
    position: relative;
    padding: 15px 0;
    animation-name: header-search; }
  .header__search-form {
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1); }
  .header__menu {
    position: relative;
    flex: 1 1 auto;
    overflow: auto;
    overflow-scrolling: touch;
    -webkit-overflow-scrolling: touch;
    max-width: 260px;
    background: #fff;
    animation-name: header-nav;
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.25); }
  .header__logo {
    flex: 1 1 auto;
    font-size: 0;
    line-height: 0;
    text-align: center; }
  .header__toggle {
    margin-right: 20px; }
  .header__phone {
    margin-left: 20px; }
  .header.header--menu-active .header__overlay, .header.header--search-active .header__overlay {
    display: block; }
  .header.header--menu-active .header__sticky, .header.header--search-active .header__sticky {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; }
  .header.header--menu-active .header__menu {
    display: block; }
  .header.header--search-active .header__search {
    display: block; }
