.section-contact__item {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding-bottom: 15px;
  margin-bottom: 20px;
  border-bottom: 1px solid #D7D7D7; }
  .section-contact__item:last-child {
    border-bottom: none;
    padding-bottom: 0;
    margin-bottom: 0; }
  .section-contact__item p {
    margin: 0 0 5px 0; }
  .section-contact__item a {
    color: currentColor;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.2em; }
  .section-contact__item__icon {
    flex: 0 0 auto;
    margin-right: 15px; }

.section-contact__footer {
  margin-top: 20px; }
