.button-menu__icon {
  width: 10px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -5px;
  margin-top: -5px;
  transition: transform 0.3s ease-out; }

.button-menu__line {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  left: 0;
  background: currentColor;
  transition: opacity 0.3s ease-out; }
  .button-menu__line:nth-child(1) {
    top: 0;
    transition: top 0.3s ease-out 0.15s, transform 0.3s ease-out; }
  .button-menu__line:nth-child(2) {
    top: 50%;
    margin-top: -1px; }
  .button-menu__line:nth-child(3) {
    bottom: 0;
    transition: bottom 0.3s ease-out 0.15s, transform 0.3s ease-out; }

.button-menu.is-active .button-menu__line:nth-child(1) {
  top: 50%;
  margin-top: -1px;
  transform: rotate(45deg);
  transition: top 0.3s ease-out, transform 0.3s ease-out 0.15s; }

.button-menu.is-active .button-menu__line:nth-child(2) {
  opacity: 0; }

.button-menu.is-active .button-menu__line:nth-child(3) {
  bottom: 50%;
  margin-bottom: -1px;
  transform: rotate(-45deg);
  transition: bottom 0.3s ease-out, transform 0.3s ease-out 0.15s; }
